.search__footer {
  border-top: 1px solid $border--light;
  display: flex;
  margin-top: unit(1.5);
  padding-top: unit(1.5);

  p {
    flex-grow: 1;
    margin: 0;
  }
}

.search__wayfinding {
  display: none;
  font: 1.8rem/3rem $font-family--serif;

  @include respond-to('medium-large') {
    display: block;
  }
}

.search__pagination {
  align-items: center;
  display: flex;
  justify-content: center;
  text-align: right;

  @include respond-to('medium-large') {
    justify-content: flex-end;
  }

  .page-numbers {
    border: 1px solid $border--light;
    color: $color--tuatara;
    display: block;
    font-size: 1.4rem;
    font-weight: $font-weight--normal;
    line-height: 4rem;
    margin-left: -1px;
    position: relative;
    text-align: center;
    text-decoration: none;
    transition: all 0.2s $ease--snappy;

    @media (max-width: map-get($breakpoints, 'small--down')) {
      flex-grow: 1;
    }

    @include respond-to('small') {
      min-width: 4rem;
      padding: 0 8px;
    }

    @include respond-to('medium') {
      line-height: 3rem;
      min-width: 3rem;
    }

    &:not(.prev):not(.next):not(.dots)::after {
      @include pseudo;
      background-color: $color--fire;
      bottom: 0;
      height: 0;
      left: 0;
      right: 0;
      transition: all 0.2s $ease--snappy;
      z-index: -1;
    }

    &:not(.prev):not(.next):not(.dots):hover,
    &:not(.prev):not(.next):not(.dots):focus {
      color: $color--white;

      &::after {
        height: 100%;
      }
    }

    &.dots {
      color: rgba($color--tuatara, 0.3);
    }

    &.current {
      color: $color--white;
      font-weight: $font-weight--bold;

      &::after {
        height: 100% !important;
      }
    }

    &.prev,
    &.next {
      border: none;
      color: $color--fire;
      font-weight: $font-weight--bold;

      @media (max-width: map-get($breakpoints, 'small--down')) {
        display: none;
      }

      @include hover {
        text-decoration: underline;
      }
    }

    &.prev {
      padding: 0 12px 0 0;
    }

    &.next {
      padding: 0 0 0 12px;
    }
  }
}
